:root {
  color: #fff;
  background-color: #242a3b;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, Helvetica, Arial, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
}

body {
  margin: 0;
  padding: 0;
}

#app {
  flex-direction: column;
  height: 100vh;
  display: flex;
}

#header {
  background-color: #1e1e1e;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  height: 30px;
  margin: 0;
  padding: 1rem 1rem 1rem .5rem;
  display: flex;
}

#content {
  height: calc(100vh - 30px);
  overflow: auto;
}

.editor-panel {
  overflow: scroll;
}

.right-panel {
  padding-left: 1rem;
  padding-right: 1rem;
  overflow: scroll;
}

.state-graph {
  background-color: #1e1e1e;
  border: 1px solid #333;
  border-radius: 6px;
  width: 100%;
  height: 50vh;
  box-shadow: 0 0 10px #0003;
}

.console {
  background-color: #1e1e1e;
  border: 1px solid #333;
  border-radius: 6px;
  max-height: 50vh;
  margin: 1rem 0;
  overflow-y: auto;
  box-shadow: 0 0 10px #0003;
}

.console pre {
  color: #b7b7b7;
  white-space: pre-wrap;
  word-wrap: break-word;
  margin: 0;
  padding: 1rem;
  font-family: Menlo, Monaco, Courier New, monospace;
  font-size: 13px;
  line-height: 1.5;
}

.console::-webkit-scrollbar {
  width: 8px;
}

.console::-webkit-scrollbar-track {
  background: #2c2c2c;
  border-radius: 4px;
}

.console::-webkit-scrollbar-thumb {
  background: #555;
  border-radius: 4px;
}

.console::-webkit-scrollbar-thumb:hover {
  background: #666;
}

.vscode-button {
  color: #ccc;
  cursor: pointer;
  background-color: #2d2d2d;
  border: 1px solid #3d3d3d;
  border-radius: 2px;
  align-items: center;
  gap: 6px;
  padding: 4px 12px;
  font-size: 13px;
  transition: background-color .2s;
  display: flex;
}

.vscode-button:hover {
  background-color: #3d3d3d;
}

.vscode-button .codicon {
  font-size: 14px;
}

.actions {
  align-items: center;
  gap: 6px;
  display: flex;
}

.brand {
  align-items: center;
  gap: 12px;
  height: 100%;
  padding: .5rem;
  display: flex;
}

.Resizable-handle {
  cursor: col-resize;
  background: none;
  border: 0;
  width: 1px;
  margin: 0;
  transition: all .2s;
  position: relative;
}

.Resizable-handle:after {
  background: #7f7f7f33;
  width: 1px;
  transition: all .2s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
}

.Resizable-handle:before {
  content: "";
  background: #7f7f7f33;
  width: 4px;
  height: 60px;
  transition: height .2s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.Resizable-handle:hover:before {
  background: #7f7f7f33;
  height: 100%;
}

.Resizable-handle:hover {
  background: #7f7f7f33;
}

.Resizable-handle:hover:after {
  background: #7f7f7f33;
  width: 2px;
}
